/**
 * Author: Nhut Le
 * Calculator object: to handle calculation functions
 */
var Calculator = function() {
  'use strict';

  var current, memory, operation;

  current   = ''; // current input
  memory    = ''; // last result
  operation = ''; // +-*:

  /**
   * Add operands to current input
   * @param {String} operand
   */
  var addOperand = function(operand) {
    current += operand;

    Displayer.showResult(current);
  };

  /**
   * Add operation
   * @param {String} op +-*:
   */
  var addOperation = function(op) {
    if (operation !== '') {
      calculate();
    }

    memory    = current;
    operation = op;
    current   = '';

    Displayer.showResult(memory);
  };

  /**
   * Manipulate calculation
   */
  var calculate = function() {
    memory  = parseFloat(memory);
    current = parseFloat(current);

    if (isNaN(memory) || isNaN(current)) {
      return;
    }

    if (operation === '+') {
      current = memory + current;
    }

    if (operation === '-') {
      current = memory - current;
    }

    if (operation === '*') {
      current = memory * current;
    }

    if (operation === '/') {
      if (current === 0) { // divided by zero
        current = 'Error';
      } else {
        current = memory / current;
      }
    }

    operation = '';
    memory    = '';

    Displayer.showResult(current);
  };

  /**
   * Reset current input
   */
  var clear = function() {
    current = '';

    Displayer.showResult(current);
  };

  /**
   * Reset the calculator
   */
  var clearAll = function() {
    current   = '';
    memory    = '';
    operation = '';

    Displayer.showResult(current);
  };

  return {
    addOperand: addOperand,
    addOperation: addOperation,
    calculate: calculate,
    clear: clear,
    clearAll: clearAll
  };
}();
/**
 * Author: Nhut Le
 * Display object:
 */

var Displayer = function(document) {
  'use strict';

  var $result, $equal, $numbers, $operators, $clear, $clearAll;

  $result    = document.getElementById('result');
  $equal     = document.getElementById('equal');
  $numbers   = document.getElementById('numbers')
                       .querySelectorAll('a:not(#clear):not(#clear_all)');
  $operators = document.getElementById('operators')
                       .querySelectorAll('a:not(#equal)');
  $clear     = document.getElementById('clear');
  $clearAll  = document.getElementById('clear_all');

  /**
   * Initialize Displayer
   */
  var initialize  = function() {
    // default value of result is zero
    $result.innerHTML = '0';

    /**
     * Attach click events to numbers
     */
    $numbers.forEach(function(element) {
      element.addEventListener('click', function() {
        Calculator.addOperand(this.text);
      });
    });

    /**
     * Attach click events to operators
     */
    $operators.forEach(function(element) {
      element.addEventListener('click', function() {
        Calculator.addOperation(this.text);
      });
    });

    /**
     * Attach click events to the rest
     */
    $equal.addEventListener('click', Calculator.calculate);
    $clear.addEventListener('click', Calculator.clear);
    $clearAll.addEventListener('click', Calculator.clearAll);
  };

  initialize();

  /**
   * Show result
   * @param  {String} result
   */
  var showResult = function(result) {
    $result.innerHTML = result;
  };

  return {
    showResult: showResult
  };
}(document);